.banner{
  background-image: url(../../../assets/images/Group\ 2.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
.tiny-text{
  padding-top: 6%;
  color: #330075;
  font-family: 'Roboto Mono', monospace;
}
.upper-text{
  font-size: 5rem;
  font-weight: bold;
  color: #3C40C6;
}
.middle-text{
  color: #6600dbc7;
  font-size: 3rem;
  animation: type 3.5s ease-in-out;
  display: inline-block;
  height: 4rem;
  overflow: hidden;
}
@keyframes type{
  0%{
    width: 0;
    
  }
  100%{
    width: 100%;
  }
}
.btn-area{
  margin-top: 4rem;
}
.btn{
  background-color: #ffff;
  border: 2px solid #3C40C6;
  padding: 12px 20px;
  border-radius: 4px;
  letter-spacing: 2px;
  color: #330075;
}
.designation-text{
  margin-top: 2rem;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #3C40C6;
}

@media only screen and (max-width: 600px){
  .middle-text{
    overflow: visible;
    font-size: 2rem;
    width: 100%;
    animation: none;
  }
}